// app/cars/[model]/page.jsx (Server Component)
import React from 'react';
import path from 'path';
import fs from 'fs/promises';
import Header from '@/components/Header/Header';
import ClientCar from './ClientCar';

// ISR: пересобираем страницу раз в минуту
export const revalidate = 60;

// Генерируем статические пути из JSON-файлов
export async function generateStaticParams() {
  const dir = path.join(process.cwd(), 'src', 'data', 'cars');
  const files = await fs.readdir(dir);
  return files
    .filter(f => f.endsWith('.json'))
    .map(f => ({ model: f.replace(/\.json$/, '') }));
}

export default async function CarPage({ params }) {
  const { model } = params;

  // Чтение статичных данных машины
  const filePath = path.join(
    process.cwd(),
    'src',
    'data',
    'cars',
    `${model}.json`
  );
  const carRaw = await fs.readFile(filePath, 'utf-8');
  const carInfo = JSON.parse(carRaw);

  // Fetch данных API на сервере с ISR
  const [modelsRes, submodelsRes, seriesRes, servicesRes] = await Promise.all([
    fetch('http://89.104.65.124/api/models/', { next: { revalidate: 60 } }),
    fetch('http://89.104.65.124/api/submodels/', { next: { revalidate: 60 } }),
    fetch('http://89.104.65.124/api/series/', { next: { revalidate: 60 } }),
    fetch('http://89.104.65.124/api/maintenance-services/', { next: { revalidate: 60 } }),
  ]);
  if (![modelsRes, submodelsRes, seriesRes, servicesRes].every(r => r.ok)) {
    throw new Error('Не удалось загрузить данные с API');
  }
  const [models, submodels, series, services] = await Promise.all([
    modelsRes.json(),
    submodelsRes.json(),
    seriesRes.json(),
    servicesRes.json(),
  ]);

  // Подготовка данных для калькулятора
  const data = models.map(m => ({
    ...m,
    submodels: submodels
      .filter(sm => sm.model.id === m.id)
      .map(sm => ({
        ...sm,
        series: series.filter(s => s.submodel.id === sm.id),
      })),
  }));

  // Формируем слайды для карусели
  const problemsImages = Array.from({ length: 6 }, (_, i) => ({
    src: `/images/car_slides/car_slide_${i + 1}.jpg`,
    caption: '',
  }));

  return (
    <>
      <Header />
      <ClientCar
        model={model}
        carInfo={carInfo}
        data={data}
        services={services}
        problemsImages={problemsImages}
      />
    </>
  );
}
